* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

html {
  scroll-behavior: smooth;
  background: linear-gradient(90deg, #2af598 0%, #009efd 100%);
}

body {
  margin: 0;
  padding-left: 10px;
  padding-right: 10px;
}

.box {
  max-width: 980px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  display: flex;
}

.searchBox {
  background: #2f3640;
  border-radius: 50px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.searchInput {
  width: 250px;
  height: 65px;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 25px 65px 25px 25px;
  font-size: 15px;
}

.searchButton {
  color: #fff;
  z-index: 2;
  width: 50px;
  height: 50px;
  background: linear-gradient(90deg, #2af598 0%, #009efd 100%);
  border: 0;
  border-radius: 50%;
  transition: all .3s cubic-bezier(.23, 1, .32, 1);
  display: inline-block;
  position: absolute;
  right: 8px;
}

.searchButton:hover {
  color: #fff;
  background-color: #1a1a1a;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, .5);
}

.searchButton:active {
  box-shadow: none;
  transform: translateY(0);
}

.svg {
  fill: #2dfd51;
}

.gallery {
  max-width: 1170px;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  border-radius: 0 0 10px 10px;
  outline: 1px solid #2dfd51;
}

.info {
  width: 270px;
  height: 62px;
  background: linear-gradient(#22c1c3 0%, #2d30fd 100%);
  border-radius: 0 0 10px 10px;
  gap: 10px;
  padding: 7px;
  display: flex;
}

.info-item {
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  display: flex;
}

.sorry {
  color: #194a0d;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 35px;
  font-weight: 700;
  display: none;
}

.scroll-top {
  width: 35px;
  height: 35px;
  background-color: #2af598;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: fixed;
  bottom: 30px;
  right: 20px;
}

.svg-up {
  fill: #009efd;
}

.scroll-down {
  width: 35px;
  height: 35px;
  background-color: #009efd;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: fixed;
  bottom: 30px;
  left: 20px;
}

.svg-down {
  fill: #2af598;
}

.voltage-button {
  width: 165px;
  margin-left: auto;
  margin-right: auto;
  display: none;
  position: relative;
}

.voltage-button button {
  color: #fff;
  letter-spacing: .075em;
  background: #0d1127;
  border: 5px solid #5978f3;
  border-radius: 5rem;
  padding: 1rem 3rem;
  font-size: 1.2rem;
  line-height: 1em;
  transition: background .3s;
}

.voltage-button button:hover {
  cursor: pointer;
  background: #0f1c53;
}

.voltage-button button:hover + svg, .voltage-button button:hover + svg + .dots {
  opacity: 1;
}

.voltage-button svg {
  width: calc(100% + .5em);
  height: calc(100% + 1.5em);
  pointer-events: none;
  opacity: 0;
  transition: opacity .4s .1s;
  display: block;
  position: absolute;
  top: -.75em;
  left: -.25em;
}

.voltage-button svg path {
  stroke-dasharray: 100;
  filter: url("#glow");
}

.voltage-button svg path.line-1 {
  stroke: #f6de8d;
  stroke-dashoffset: 0;
  animation: spark-1 3s linear infinite;
}

.voltage-button svg path.line-2 {
  stroke: #6bfeff;
  stroke-dashoffset: 500px;
  animation: spark-2 3s linear infinite;
}

.voltage-button .dots {
  opacity: 0;
  transition: opacity .3s .4s;
}

.voltage-button .dots .dot {
  width: 1rem;
  height: 1rem;
  opacity: 0;
  background: #fff;
  border-radius: 100%;
  position: absolute;
}

.voltage-button .dots .dot-1 {
  animation: fly-up 3s linear infinite;
  top: 0;
  left: 20%;
}

.voltage-button .dots .dot-2 {
  animation: fly-up 3s linear .5s infinite;
  top: 0;
  left: 55%;
}

.voltage-button .dots .dot-3 {
  animation: fly-up 3s linear 1s infinite;
  top: 0;
  left: 80%;
}

.voltage-button .dots .dot-4 {
  animation: fly-down 3s linear 2.5s infinite;
  bottom: 0;
  left: 30%;
}

.voltage-button .dots .dot-5 {
  animation: fly-down 3s linear 1.5s infinite;
  bottom: 0;
  left: 65%;
}

@keyframes spark-1 {
  to {
    stroke-dashoffset: -1000px;
  }
}

@keyframes spark-2 {
  to {
    stroke-dashoffset: -500px;
  }
}

@keyframes fly-up {
  0% {
    opacity: 0;
    transform: translateY(0)scale(.2);
  }

  5% {
    opacity: 1;
    transform: translateY(-1.5rem)scale(.4);
  }

  10%, 100% {
    opacity: 0;
    transform: translateY(-3rem)scale(.2);
  }
}

@keyframes fly-down {
  0% {
    opacity: 0;
    transform: translateY(0)scale(.2);
  }

  5% {
    opacity: 1;
    transform: translateY(1.5rem)scale(.4);
  }

  10%, 100% {
    opacity: 0;
    transform: translateY(3rem)scale(.2);
  }
}

